.card{
    width: 40%;
    min-width: 250px;
    display: flex;
    flex-direction: column;
    margin: 5%;
    justify-content: center;
    align-items: center;
    background-color: #23252c;
    padding: 1.5em 0;
}
.dark-card{
    background-color: #23252c;
}
.light-card{
    background-color: #F5F5F5;
}
.card img{
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    width: 60%;

}
