.container {
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.dark-container{
    background-color: #1a1b21;
}
.light-container{
    background-color: #F5F5F5;
    ;
}

/* Author info */

.author-name {
  
    font-size: 1.6em;
    margin: 0.7em 0 0 0;
}

.author {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.author p {
    font-size: 0.7em;
    margin: 0 0 0.2em 0;
    color: #F3BF99;
}
.dark-text{
    color: black;
}
.light-text{
    color: white;

}
.blue-text{
    color: #DCDCDC;

}

.author a {
    font-size: 0.6em;
    text-decoration: none;
    color: #bebfc0;
    margin: 0.5em 0 1em 0;
}

/* Content */
.contact {
    display: flex;
    width: 80%;
    justify-content: center;
    align-items: center;
}

button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.6em;
    width: 50%;
    margin: 0.4em;
    font-size: 0.8em;
    border-style: none;
    border-radius: 0.4em;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}
button > svg{
    margin-right: 0.2em;
}

button:hover {
    cursor: pointer;
}

.linkedin {
    background-color: #5093E2;
}

.content{
    width: 80%; 
    margin: 1em 0;
}
.content h1{
    font-weight: 700;
    font-size: 1em;
}
.content p{
    font-weight: 400;
    font-style: normal;
    font-size: 0.6em;
}