.footer {
  
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.footer-dark{
    background-color: #161619;
}
.footer-light{
    background-color: #D5D4D8;
}

ul {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
}

ul li {
    margin: 0.6em;
}

ul li a {
    text-decoration: none;
    color: #918E9B;
}