html {
    background-color: #e5e5e5;
    font-family: 'Inter', sans-serif;
    font-size: 1.2vw;
}

.App {
    background-color: #1C1C1C;
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

@media screen and (max-width : 50em) {
    html{ font-size: 0.8em;}
}